import { socket } from "./app/message"
import handleSocketEvents from "./app/message/handleSocketEvents"

const PreviousQueries = {
    channels: {},
    serverMembers: {},
    logs: [],
    at(type, key) {
        return (fct) => {
            const channelsIds = Object.keys(this.channels)
            if (channelsIds.length > 9) {
                delete this.channels[channelsIds[Math.floor(Math.random() * channelsIds.length)]]
            }
            if (typeof fct === "function") return this[type][key] = fct(this[type][key] || [])
            this[type][key] = fct
        }
    },
    init() {
        socket.on("message", msg => {
            const setMessages = PreviousQueries.at("channels", msg.channel)
            handleSocketEvents.message(setMessages, msg)
        })
        socket.on("messageDelete", msg => {
            const setMessages = PreviousQueries.at("channels", msg.channel)
            handleSocketEvents.messageDelete(setMessages, msg)
        })
        socket.on("messageEdit", msg => {
            const setMessages = PreviousQueries.at("channels", msg.channel)
            handleSocketEvents.messageEdit(setMessages, msg)
        })
        socket.on("memberUpdate", member => {
            for (const key in this.channels) {
                const setMessages = PreviousQueries.at("channels", key)
                handleSocketEvents.memberUpdate(() => {}, setMessages, member)
            }
            for (const key in this.serverMembers) {
                const setMembers = PreviousQueries.at("serverMembers", key)
                handleSocketEvents.memberUpdate(setMembers, () => {}, member)
            }
        })
        socket.on("disconnect", this.clear)
    },
    clear() {
        for (const channel in this.channels) {
            delete this.channels[channel]
        }
    }
}

export default PreviousQueries