import { useContext } from "react"
import User from "."
import { ScreenContext } from "../../app/message"
import Gear from "../../icons/settings"
import StatusManager from "../../app/message/statusManager"

const Footer = () => {
    const { user, setView, setShowSettings } = useContext(ScreenContext)
    const me = new User(user)

    const showSettings = () => setShowSettings(true)
    /*const save = () => {
        setView(<UserSettings onEdit={editHandler} user={me} name="bigger waiting" onClickOut={disappear} />)
    }
    const appear = () => setView(<UserSettings onEdit={editHandler} user={me} name="bigger" onClickOut={disappear} onSave={save} />)
    const disappear = () => {
        setView(<UserSettings onEdit={editHandler} user={me} name="smaller" onClickOut={disappear} />)
        setTimeout(() => setView(), 100)
    } */


    return (
        <footer className="footer">
            <div className="icon-div">
                <img onClick={() => setView(<StatusManager />)} width="32" height="32" className="my-pfp" src={me.displayAvatarURL(90, "false")} alt="icon" />
                <div className="status icon-status" />
            </div>
            <h5 className="my-username">{me.username}<span>{me.tag}</span></h5>
            <div title="User Settings" onClick={showSettings} className="settings-button"><Gear width={17} height={17} /></div>
        </footer>
    )
}

export default Footer