import { useContext } from "react"
import { ScreenContext } from ".."
import User from "../../../classes/user"
import Animate from "../../animate"
import "./index.css"

const StatusManager = () => {
    const { user } = useContext(ScreenContext)
    const me = new User(user)

    return (
        <Animate normal type={1}>
            <div style={{ width: 220, left: 84, bottom: 62, transformOrigin: "bottom center" }} className="status-manager generic-menu animated-popup">
                <div className="current-status option">{me.customStatus ? me.customStatus : "Set a custom status"}</div>
            </div>
        </Animate>
    )
}

export default StatusManager