import { useContext, useState } from "react"
import { ScreenContext } from "../../app/message"
import Crown from "../../icons/crown"
import Utils from "../../utils"
import UserJSX from "./user"

const MemberJSX = ({ server, member }) => {
    const [isClicked, setIsClicked] = useState(false)
    const { setView } = useContext(ScreenContext)
    const showUser = (e) => {
        setIsClicked(true)
        const cancel = () => {
            setIsClicked(false)
            setView()
        }
        const top = e.target.getBoundingClientRect().top - 40
        const clickOut = (...args) => {
            setIsClicked(false)
            setView(...args)
        }
        setView(
            <Utils.Out click={cancel}>
                <UserJSX user={member} setView={clickOut} style={{ right: 245, top, animationName: "reverseShift" }} server={server}/>
            </Utils.Out>
        )
    }
    return (
        <div onClick={showUser} key={member.id} className={`member ${isClicked && "member-clicked"} ${!member.online && "member-offline"}`}>
            <div className="member-avatar">
                <img width="32" height="32" src={member.displayAvatarURL(90, "false")} alt="avatar" className={`member-icon`} />
                <div className="member-is-online" />
            </div>
            <div className="nice-div">
                <span className={`member-name ${server?.owner === member.id && "owner"}`}>{member.username} {server?.owner === member.id && <Crown size={16} />}</span>
                <span className="is-online">{member.online ? "Online" : "Offline"}</span>
            </div>
        </div>
    )
}

export default MemberJSX